import React from 'react'
import './Card.css'
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import Badge from '@mui/material/Badge';
import { Link } from 'react-router-dom';


function CardComponent(props) {
    localStorage.setItem('currentPage', JSON.stringify(props.page))
    return (
        <Badge anchorOrigin={{ horizontal: 'left', vertical: 'top' }} badgeContent={props.status} color={props.status === 'Alive' ? 'success' : props.status === 'Dead' ? 'error' : 'warning'}>
            <Link to={`/Character/${props.name}`} state={{ id: props.id }} style={{textDecoration:"none"}}>
                <Card sx={{ maxWidth: 345 }}>
                    <CardActionArea>
                        <CardMedia
                            component="img"
                            image={props.image}
                            alt={props.name}
                        />
                        <CardContent>
                            <Typography gutterBottom variant="h6" component="div">
                                {props.name}
                            </Typography>
                        </CardContent>
                    </CardActionArea>
                </Card>
            </Link>
        </Badge>
    )
}

export default CardComponent