import React from 'react'
import { useState, useEffect } from 'react';
import axios from 'axios';
import { DataGrid } from '@mui/x-data-grid'
import './Table.css'
import Fab from '@mui/material/Fab';
import { Trash } from 'react-bootstrap-icons';


const Table = (props) => {
    const [data, setData] = useState([])
    const [selectedRows, setSelectedRows] = useState([])
    const filter = props.filter
    console.log(data);

    useEffect(() => {
        const sendReq = async () => {
            await axios.get('/').then((res) => {
                setData(res.data.results)
            }).catch((err) => console.log(err))
        }
        sendReq()
    }, [])

    const filterData = (item) => {
        for (var key in filter) {
            if (filter[key] !== "All" && filter[key] !== item[key]) {
                return false;
            }
        }
        return true;
    }
    const filtered = data.filter(filterData);

    const fabStyle = {
        position: 'absolute',
        bottom: 16,
        right: 16,
        backgroundColor: 'rgb(173, 27, 27)',
        color: 'white'
    };

    const columns = [
        { field: 'id', headerName: 'ID', width: 70 },
        { field: 'name', headerName: 'Name', width: 130 },
        { field: 'status', headerName: 'Status', width: 130 },
        {
            field: 'species',
            headerName: 'species',
            width: 90,
        },
    ];

    const deleteRowHandler = (row) => {
        const newData = [...data]
        row.map(item => {
            const foundedItem = newData.findIndex(e => {
                return e.id === item.id
            })
            newData.splice(foundedItem, 1)
        })
        setData(newData)
    }

    return (
        <div style={{ height: 400, width: '100%' }}>
            <DataGrid
                rows={filtered}
                columns={columns}
                onRowSelectionModelChange={(ids) => {
                    const selectedRowsData = ids.map((id) => data.find((row) => row.id === id));
                    setSelectedRows(selectedRowsData)
                }}
                initialState={{
                    pagination: {
                        paginationModel: { page: 0, pageSize: 5 },
                    },
                }}
                pageSizeOptions={[5, 10]}
                checkboxSelection
            />
            <Fab sx={fabStyle} onClick={() => deleteRowHandler(selectedRows)}>
                {<Trash />}
            </Fab>
        </div>
    )
}

export default Table