import React from 'react'
import { Button, Pagination } from 'react-bootstrap'
import { ChevronDoubleLeft, ChevronDoubleRight, ChevronLeft, ChevronRight } from 'react-bootstrap-icons'
import './Pagination.css'
const PaginationHandler = (props) => {
    const nPage = Math.ceil(props.data.length / props.recordsPerPage)
    const numbers = [...Array(nPage + 1).keys()].slice(1)
    return (
        <Pagination color="dark">
            {props.currentPage !== numbers[0] ? (
                <>
                    {props.currentPage > numbers[1] ? <Button className='arrow-btn' onClick={() => props.setCurrentPage(numbers[0])}><ChevronDoubleLeft className='icon' /></Button> : []
                    }
                    <Button className='arrow-btn' onClick={() => props.currentPage !== 1 ? props.setCurrentPage(props.currentPage - 1) : []}><ChevronLeft className='icon' /></Button>
                </>
            ) : []}
            {numbers.map((item, index) => {
                return (
                    <Button onClick={() => props.setCurrentPage(item)} className={`number-btn ${props.currentPage === item ? 'active' : ''}`} key={index}>{item}</Button>
                )
            })}
            {props.currentPage !== numbers.length ? (
                <>
                    <Button className='arrow-btn' onClick={() => props.currentPage !== nPage ? props.setCurrentPage(props.currentPage + 1) : []} ><ChevronRight className='icon' /></Button>
                    {props.currentPage < numbers.pop() - 1 ? <Button className='arrow-btn' onClick={() => props.setCurrentPage(numbers.pop()+1)}><ChevronDoubleRight className='icon' /></Button>
                        : []
                    }
                </>
            ) : []}
        </Pagination>
    )
}

export default PaginationHandler