import * as React from 'react';
import { useState } from 'react';
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';
import ShowPosts from '../Posts/ShowAll/ShowPosts';
import Table from '../Table/Table';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';


const Nav = () => {
    const [tabContent, setTabContent] = useState('Cards');
    const [status, setStatus] = useState('All');
    const [gender, setGender] = useState('All');
    const [species, setSpecies] = useState('All');
    const [cardCurrentPage, setCardPage] = useState(1)

    const statusChangeHandler = (event) => {
        setCardPage(1)
        setStatus(event.target.value);
    };
    const genderChangeHandler = (event) => {
        setCardPage(1)
        setGender(event.target.value);
    };
    const speciesChangeHandler = (event) => {
        setCardPage(1)
        setSpecies(event.target.value);
    };

    const TabChanheHandler = (event, newValue) => {
        setTabContent(newValue);
    }

    return (
        <Box sx={{ width: '100%' }}>
            <TabContext value={tabContent}>
                <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
                    <TabList onChange={TabChanheHandler}>
                        <Tab label="Cards" value="Cards" />
                        <Tab label="Table" value="Table" />
                    </TabList>
                    <Box ml={1} mt={2} sx={{ display: 'flex', alignItems: 'center' }}>
                        <h5>Fillter by:</h5>
                        <FormControl variant="standard" sx={{ m: 1, minWidth: 120 }}>
                            <Select
                                value={status}
                                onChange={statusChangeHandler}
                                displayEmpty
                                inputProps={{ 'aria-label': 'Without label' }}
                            >
                                <MenuItem value="All">
                                    <em>Status</em>
                                </MenuItem>
                                <MenuItem value='Alive'>Alive</MenuItem>
                                <MenuItem value='Dead'>Dead</MenuItem>
                                <MenuItem value='unknown'>Unkown</MenuItem>
                            </Select>
                        </FormControl>
                        <FormControl variant="standard" sx={{ m: 1, minWidth: 120 }}>
                            <Select
                                value={gender}
                                onChange={genderChangeHandler}
                                displayEmpty
                                inputProps={{ 'aria-label': 'Without label' }}
                            >
                                <MenuItem value="All">
                                    <em>Gender</em>
                                </MenuItem>
                                <MenuItem value='Male'>Male</MenuItem>
                                <MenuItem value='Female'>Female</MenuItem>
                                <MenuItem value='unknown'>Unkown</MenuItem>
                            </Select>
                        </FormControl>
                        <FormControl variant="standard" sx={{ m: 1, minWidth: 120 }}>
                            <Select
                                value={species}
                                onChange={speciesChangeHandler}
                                displayEmpty
                                inputProps={{ 'aria-label': 'Without label' }}
                            >
                                <MenuItem value="All">
                                    <em>Species</em>
                                </MenuItem>
                                <MenuItem value='Human'>Human</MenuItem>
                                <MenuItem value='Alien'>Alien</MenuItem>
                                <MenuItem value='unknown'>Unkown</MenuItem>
                            </Select>
                        </FormControl>
                    </Box>
                </Box>
                <TabPanel value="Cards"><ShowPosts currentPage={cardCurrentPage} setPage={setCardPage} filter={{ status: status , gender: gender , species : species }} /></TabPanel>
                <TabPanel value="Table"><Table filter={{ status: status , gender: gender , species : species }} /></TabPanel>
            </TabContext>
        </Box>
    )
}

export default Nav