import React, { useEffect, useMemo, useState } from 'react'
import axios from 'axios'
import Loader from '../../Loader/Loader'
import CardComponent from '../../Card/Card'

import Grid from '@mui/material/Grid';
import PaginationHandler from '../../Pagination/Pagination'
import { Container } from '@mui/material';


const ShowPosts = (props) => {
    document.title = 'Characters'
    const [data, setData] = useState([])
    const [isLoading, setLoading] = useState(true)
    const filter = props.filter
    const recordsPerPage = 8
    const lastIndex = recordsPerPage * props.currentPage
    const firstIndex = lastIndex - recordsPerPage

    useEffect(() => {
        props.setPage(JSON.parse(localStorage.getItem('currentPage')))
        const sendReq = async () => {
            await axios.get('/').then((res) => {
                setData(res.data.results)
                setLoading(false)
            }).catch((err) => console.log(err))
        }
        sendReq()
    }, [])

    const filterData = (item) => {
        for (let key in filter) {
            if (filter[key] !== "All" && filter[key] !== item[key]) {
                return false;
            }
        }
        return true;
    }
    const filtered = data.filter(filterData);
    const records = filtered.slice(firstIndex, lastIndex)

    return (
        <Container>
            {isLoading ? (
                <Loader />
            ) : (
                <Grid container spacing={{ xs: 2, md: 3 }} columns={{ xs: 4, sm: 10, md: 11 , lg: 15 }}>
                    {records.map((item, index) => {
                        return (
                            <Grid item xs={2} sm={4} md={3} key={index}>
                                <CardComponent status={item.status} page={props.currentPage} id={item.id} image={item.image} name={item.name} />
                            </Grid>
                        )
                    })}
                    <div className='mt-2 d-flex justify-content-center w-100'>
                        <PaginationHandler data={filtered} recordsPerPage={recordsPerPage} currentPage={props.currentPage} setCurrentPage={props.setPage} />
                    </div>
                </Grid>
            )}
        </Container>
    )
}

export default ShowPosts