import axios from 'axios'
import React, { useEffect, useState } from 'react'
import {Image, Row, Col } from 'react-bootstrap'
import { Link, useLocation } from 'react-router-dom'
import Loader from '../../Loader/Loader'

const ShowOne = () => {

    const location = useLocation()
    const [characterData, setCharacterData] = useState([])
    const [isLoading, setLoading] = useState(true)
    document.title = 'Character | ' + characterData.name
    useEffect(() => {
        const characterPage = async () => {
            await axios.get(`/${location.state.id}`).then((res) => { setCharacterData(res.data); setLoading(false) })
        }
        characterPage()
    }, [])

    return (
        <Row className='mt-2'>
            {isLoading ? (
                <Loader />
            ) : (
                <Col>
                    <Image rounded fluid src={characterData.image} />
                    <h3 className='text-center'>{characterData.name}</h3>
                    <ul style={{ listStyle: 'none', width: 'fit-content' }} className='d-flex p-0 mx-auto'>
                        <li className='text-muted mx-2'>{characterData.status}</li>
                        <li className='text-muted mx-2'>{characterData.gender}</li>
                        <li className='text-muted mx-2'>{characterData.species}</li>
                    </ul>
                    <Link to='/'>Back Home</Link>
                </Col>
            )}
        </Row>
    )
}

export default ShowOne